import React, {Component} from 'react';
import './table.component.css';

class TableComponent extends Component {

  getClassNameByStatus(status, mark) {
    if (mark) return 'checked';
    switch (status) {
      case 'Checked': {
        return 'unchecked';
      }
      case 'Checking': {
        return 'checking';
      }
      case 'ToDo': {
        return 'todo';
      }
      case 'In Progress': {
        return 'in-progress';
      }
      default:
        return;
    }
  }

  render() {
    const PadawansHeader = () => this.props.mentor.students.map((item, index) => {
      return (<th key={index}><a href={item.github}>{item.shortgithub}</a></th>);
    });

    const StudentCell = ({task, status}) => this.props.mentor.students.map((student, index) => {
      const taskExist = student.tasks.find((item) => item.name === task);
      let mark;
      if (taskExist) mark = taskExist.mark;

      return (
        <td key={index} className={this.getClassNameByStatus(status, mark)}>{mark}</td>
      );
    });

    const TaskLine = () => this.props.tasks.map((task, index) => {
      return (
        <tr key={index}>
          <td><a href={task.link}>{task.name}</a></td>
          <StudentCell task={task.name} status={task.status}/>
        </tr>
      );
    });


    return (
      <div className='table'>
        <table>
          <tbody>
          <tr className='headers'>
            <th>Tasks / Padawans</th>
            <PadawansHeader/>
          </tr>
          <TaskLine/>
          </tbody>
        </table>
      </div>
    );
  }
}

export default TableComponent;
