import React, { Component } from 'react';
import Select from 'react-select';
import TableComponent from '../table/table.component';
import './search.component.css';

const pathToDataFile = './data.json';
const preloadedMentor = {
  value: '',
  label: ''
};
let data;

function getMentorsWithShortGithub(obj, inputValue) {
  const arr = [];
  obj.mentors.forEach((item) => {
    const mentor = {
      value: item.shortgithub.toLowerCase(),
      label: item.shortgithub,
    };
    if (mentor.label.toLowerCase().includes(inputValue.toLowerCase())) arr.push(mentor);
  });
  return arr;
}

function getMentorByGithub(obj, github) {
  let mentor = undefined;
  obj.mentors.forEach((item) => {
    if (item.shortgithub.toLowerCase() === github.toLowerCase()) mentor = item;
  });
  return mentor;
}

function getFile(src) {
  return new Promise((resolve, reject) => {
    const http = new XMLHttpRequest();
    http.overrideMimeType('application/json');
    http.open('GET', src, true);
    http.onreadystatechange = () => {
      if (http.readyState === 4 && http.status === 200) {
        resolve(http.responseText);
      }
    };
    http.onerror = () => reject(new Error('Network Error'));
    http.send();
  });
}

class SearchComponent extends Component {
  state = {
    inputValue: '',
    value: '',
    openTable: false,
  };

  handleInputChange = (newValue) => {
    this.setState({
      inputValue: newValue,
    });
  };

  getLoadedUser = () => {
    let storage = localStorage.getItem('mentorDashboard');
    let user = '';
    if (storage) {
      storage = JSON.parse(storage);
      user = this.getUser(storage, storage.current);
    }
    return user;
  };

  getUser(storage, name) {
    let user;
    for (let i = 0, len = storage.users.length; i < len; i++) {
      if (storage.users[i].name === name) {
        user = storage.users[i];
        break;
      }
    }
    return user;
  }

  componentDidMount() {
    getFile(pathToDataFile)
      .then(rez => {
        data = JSON.parse(rez);
        const user = this.getLoadedUser();
        preloadedMentor.value = user.chosenMentor;
        preloadedMentor.label = user.chosenMentor;
        this.handleInputChange(user.chosenMentor);
        this.setState({
          value: user.chosenMentor,
          openTable: user.chosenMentor !== ''
        });
        this.forceUpdate();
      })
      .catch(error => alert(error));
  }

  handleChange = (newObj) => {
    if (newObj) {
      const { value } = newObj;
      let storage = localStorage.getItem('mentorDashboard');
      if (storage) {
        storage = JSON.parse(storage);
        let user = this.getUser(storage, storage.current);
        user.chosenMentor = value;
        localStorage.setItem('mentorDashboard', JSON.stringify(storage));
      }
      this.setState({
        value: value,
        openTable: true
      });
    }
  };

  render() {
    let table;
    if (this.state.openTable) {
      table = (<TableComponent
        key={1}
        tasks={data.tasks}
        mentor={getMentorByGithub(data, this.state.value)}
      />);
    }

    return (
      <section className='search-component'>
        <Select
          isClearable
          defaultValue={preloadedMentor}
          onChange={this.handleChange}
          onInputChange={this.handleInputChange}
          options={
            data
              ? getMentorsWithShortGithub(data, this.state.inputValue)
              : []
          }
        />
        {table}
      </section>
  );
  }
}

export default SearchComponent;
