import React, {Component} from 'react';
import './login.component.css';

class Login extends Component {

  state = {
    value: '',
    valueError: '',
    password: '',
    passwordError: '',
    error: '',
    message: ''
  };

  getStorage() {
    let storage = localStorage.getItem('mentorDashboard');
    if (storage) storage = JSON.parse(storage);
    else {
      storage = {
        users: [],
        current: '',
      };
    }
    return storage;
  }

  getUser(storage) {
    let user;
    for (let i = 0, len = storage.users.length; i < len; i++) {
      if (storage.users[i].name === this.state.value && storage.users[i].password === this.state.password) {
        return storage.users[i];
      }
    }
    return user;
  }

  handleLogin = (event) => {
    event.preventDefault();
    let storage = this.getStorage();
    const user = this.getUser(storage);

    if (user) {
      storage.current = user.name;
      localStorage.setItem('mentorDashboard', JSON.stringify(storage));
      this.props.history.push('dashboard-test/searching');
    } else this.setState({error: 'You aren\'t registered.'});
  };

  isValidPassword = (name) => {
    let flag = true;

    this.setState({passwordError: ''});
    if (name.length < 3) {
      flag = false;
      this.setState({passwordError: 'So short password. Make it bigger!'});
    }
    if (name.includes(' ')) {
      flag = false;
      this.setState({passwordError: 'Remove whitespaces.'});
    }

    return flag;
  };

  isValidName = (name) => {
    let flag = true;

    this.setState({valueError: ''});
    if (name.length < 3) {
      flag = false;
      this.setState({valueError: 'So short name. Add something!'});
    }
    if (name.includes(' ')) {
      flag = false;
      this.setState({valueError: 'Remove whitespaces.'});
    }

    return flag;
  };

  handleRegister = (event) => {
    event.preventDefault();
    let storage = this.getStorage();
    let user = this.getUser(storage);

    if (!this.isValidName(this.state.value)) return;
    if (!this.isValidPassword(this.state.password)) return;
    if (user) {
      this.setState({error: 'The user is already exist!'});
    } else {
      storage.users.push({
        name: this.state.value,
        password: this.state.password,
        chosenMentor: '',
      });
      this.setState({message: 'You are registered.'});
      localStorage.setItem('mentorDashboard', JSON.stringify(storage));
    }
  };

  handleClick = (e) => {
    e.preventDefault();
    this.setState({error: '', message: ''});
    const registerFormEl = document.querySelector('.register-form');
    const loginFormEl = document.querySelector('.login-form');
    registerFormEl.classList.toggle('-hide');
    loginFormEl.classList.toggle('-hide');
  };

  handleOnChange = ({target}) => {
    if (target.type === 'text') this.setState({value: target.value});
    if (target.type === 'password') this.setState({password: target.value});
  };

  render() {
    return (
      <div className="login-page">
        <div className="form">
          <form className="register-form -hide">
            <input type="text" placeholder="name" value={this.state.value} onChange={this.handleOnChange}/>
            <p className='warning'>{this.state.valueError}</p>
            <input type="password" placeholder="password" value={this.state.password} onChange={this.handleOnChange}/>
            <p className='warning'>{this.state.passwordError}</p>
            <button onClick={this.handleRegister}>create</button>
            <p className='warning'>{this.state.error}</p>
            <p className='attention'>{this.state.message}</p>
            <p className="message">Already registered? <a onClick={this.handleClick} href="/">Sign In</a></p>
          </form>
          <form className="login-form" onSubmit={this.handleLogin}>
            <input type="text" placeholder="username" value={this.state.value} onChange={this.handleOnChange}/>
            <input type="password" placeholder="password" value={this.state.password} onChange={this.handleOnChange}/>
            <button onClick={this.handleLogin}>login</button>
            <p className='warning'>{this.state.error}</p>
            <p className="message">Not registered? <a onClick={this.handleClick} href="/">Create an account</a></p>
          </form>
        </div>
      </div>
    );
  }
}

export default Login;
